"use client";
import React, { useState } from "react";
import PersonalDetails from "../components/detailspersonal";
import ProfessionalDetails from "../components/ProfessionalDetails";
import EducationDetails from "../components/EducationDetails";

export default function Dashboard() {
  const [tab, setTab] = useState("personal");

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <div className="max-w-5xl mx-auto bg-white shadow-md rounded-xl">
        <div className="flex justify-around border-b">
          {["personal", "professional", "education"].map((t) => (
            <button
              key={t}
              onClick={() => setTab(t)}
              className={`px-6 py-3 font-semibold capitalize border-b-4 ${
                tab === t ? "border-blue-600 text-blue-600" : "border-transparent text-gray-500"
              }`}
            >
              {t} Details
            </button>
          ))}
        </div>
        <div className="p-6">
          {tab === "personal" && <PersonalDetails />}
          {tab === "professional" && <ProfessionalDetails />}
          {tab === "education" && <EducationDetails />}
          
        </div>
      </div>
    </div>
  );
}
