export default function PersonalDetails({ user }) {
  return (
    <form className="space-y-4">
      <h2 className="text-xl font-bold text-blue-700">Personal Information</h2>

      <input type="text" value={user?.name || ""} readOnly />
      <input type="email" value={user?.email || ""} readOnly />
      <input type="text" value={user?.phone || ""} readOnly />
      <input type="text" value={user?.experienceLevel || ""} readOnly />
      <input type="text" value={user?.workStatus || ""} readOnly />
    </form>
  );
}
